import React from 'react'
import './Settings.css'
import { Col, Container, Nav, Row } from 'react-bootstrap'
import { Link, Outlet } from 'react-router-dom'

export default function Settings() {
    return (
        <Container>
            <Nav className='Settings-Nav' justify variant="tabs" defaultActiveKey="/home">
                <Nav.Item>
                    <Link to="Home">  </Link>
                </Nav.Item>
                <Nav.Item>
                    <Link to="Movie"> </Link>
                </Nav.Item>
                <Nav.Item>
                    <Link to="Series"> </Link>
                </Nav.Item>
                <Nav.Item>
                    <Link to="Slider"> </Link>
                </Nav.Item>

                <Nav.Item>
                    <Link to="Box-Office">  </Link>
                </Nav.Item>
                <Nav.Item>
                    <Link to="other"></Link>
                </Nav.Item>

            </Nav>

            <br /><br />
            <Outlet></Outlet>
        </Container>
    )
}
